/*******************************************************************************
 * Styles for the base elements of the theme.
 ******************************************************************************/

// Typography

a, table.table a {
  color: $theme-color;
  &:hover {
    color: $theme-color;
    text-decoration: underline;
  }
  &:hover:after {
    width: 100%;
  }
}


// Math

.equation {
  margin-bottom: 1rem;
  text-align: center;
}

// Caption

.caption {
  font-size: 0.875rem;
  margin-top: 0.75rem;
  margin-bottom: 1.5rem;
  text-align: center;
}





// Navbar customization

.navbar {
  box-shadow: none;
  border-bottom: 1px solid $grey-color-light;
  opacity: 0.95;
}
.navbar.navbar-light {
  // Remove link decoration
  a {
    &:hover {
      text-decoration: none;
    }
  }
  .navbar-nav .nav-item .nav-link {
    &:hover {
      color: $theme-color;
    }
  }
  .navbar-nav .nav-item.active>.nav-link {
      background-color: inherit;
      color: $theme-color;
      font-weight: bolder;
      &:hover {
        color: $theme-color;
      }
  }
  .contact-icon {
    font-size: 2rem;
    a {
      color: $grey-color-dark;
      &:hover {
        color: $theme-color;
      }
    }
  }
}

.navbar-toggler {
  .icon-bar {
    display: block;
    width: 22px;
    height: 2px;
    background-color: $grey-color-dark;
    border-radius: 1px;
    margin-bottom: 4px;
    transition: all 0.2s;
  }
  .top-bar {
    transform: rotate(45deg);
    transform-origin: 10% 10%;
  }
  .middle-bar {
    opacity: 0;
  }
  .bottom-bar {
    transform: rotate(-45deg);
    transform-origin: 10% 90%;
  }
}

.navbar-toggler.collapsed {
  .top-bar {
    transform: rotate(0);
  }
  .middle-bar {
    opacity: 1;
  }
  .bottom-bar {
    transform: rotate(0);
  }
}


// News

.news table td {
  font-size: 1rem;
}


// Social (under photo)

.name {
  margin-top: 0.5rem;
  margin-bottom: 0.25rem;
  text-align: center;
  font-size: 1.25em;
}

.affiliation {
  margin-top: 0.5rem;
  margin-bottom: 0.25rem;
  text-align: center;
  font-size: 0.95em;
}

.social {
  margin-top: 0.5rem;
  margin-bottom: 0.25rem;
  text-align: center;
  font-size: 0.95em;
}


// Distill

.distill {
  a:hover {
    border-bottom-color: $theme-color;
    text-decoration: none;
  }
}



// Publications

.publications {
  margin: 0 auto 2rem;
  max-width: 880px;      /* readable width */
  padding: 0 1rem;       /* mobile gutter */

  /* Year headers / dividers */
  h2.year {
    color: $grey-color-dark;
    border-top: 1px solid $grey-color-light;
    padding-top: 2.5rem;
    margin: 0 0 0.5rem;
    text-align: right;
    font-size: 0.9em;
    font-style: italic;
    letter-spacing: .02em;
  }

/* Section titles */
h3 {
  font-size: clamp(1.5rem, 2vw, 1.9rem);
  padding-top: 0;              /* remove the tiny extra bump */
  text-align: center;
  margin: 0.6rem 0 0.4rem;     /* much tighter vertical spacing */
  font-weight: 250;            /* looks more balanced than 350 */
}


  /* Right-side date (month/year) */
  .pub-right {
    color: lighten($grey-color-dark, 20%); /* or #999 if vars not available */
    font-style: italic;
    font-size: 0.85em;
    text-align: right;
    white-space: nowrap;                    /* keep “July 2025” together */
    margin-left: .5rem;                     /* tiny separation from title */
  }

  /* Bibliography list */
  ol.bibliography {
    border-bottom: 1px solid $grey-color-light;
    list-style: none;
    padding: 0;
    margin: 0;

    li {
      padding: .15rem 0 .1rem;              /* tighter, consistent spacing */

      /* LINE 1: title + (with ...) + date — use grid for perfect wrapping */
      .pub-line {
        display: grid;
        grid-template-columns: 1fr auto;    /* left grows, right fits content */
        column-gap: .4rem;
        align-items: baseline;
      }

      .pub-left {
        flex: 1 1 260px;
        min-width: 0;                       /* allow wrapping within left */
      }

      /* Title (slightly less bold than default <b>) */
      .title {
        font-weight: 600;
        line-height: 1.25;
        margin-right: .25em;
      }

      /* Authors inline with title; keep pink dashed links */
      .author {
        display: inline-block;                     /* ensure inline next to title */
        margin-left: .0em;
        color: $grey-color;
        font-style: italic;

        a {
          border-bottom: 1px dashed $theme-color;
          &:hover {
            border-bottom-style: solid;
            text-decoration: none;
          }
        }
        > em {                                /* if you ever emphasize your name */
          border-bottom: 1px solid;
          font-style: normal;
        }
      }

      /* LINE 2: journal / note on its own line */
      .periodical {
        display: block;
        font-size: 0.9em;
        margin-top: .2rem;
        color: $grey-color;
        font-style: italic;
      }

      /* LINE 3: action links (keep your existing button styles) */
      .links {
        display: flex;
        flex-wrap: wrap;
        gap: .3rem .4rem;                    /* modest gap; not clumpy */
        margin-top: .15rem;
      }

      /* Abstract + bibtex buttons still look/behave the same */
      a.abstract, a.bibtex { cursor: pointer; }

      /* Abstract animation block */
      .hidden {
        font-size: 0.875rem;
        max-height: 0;
        overflow: hidden;
        text-align: justify;
        -webkit-transition: 0.15s ease;
        -moz-transition: 0.15s ease;
        -ms-transition: 0.15s ease;
        -o-transition: 0.15s ease;
        transition: max-height 0.15s ease;

        p { line-height: 1.5; margin: 10px; }
        pre { font-size: 1em; line-height: 1.4; padding: 10px; }
      }
      .hidden.open { max-height: 100em; }

      /* Abstract border + ensure block to animate */
      span.abstract.hidden {
        border: 1px dashed white;
        display: block;
        width: 100%;
        margin-top: .35rem;
      }
      span.abstract.hidden.open { border-color: $grey-color; }
    }
  }

  /* Small-screen: date drops BELOW and stays right-aligned (fixes bad wraps) */
  @media (max-width: 900px) {
    ol.bibliography li .pub-line {
      grid-template-columns: 1fr;   /* single column */
    }
    .pub-right {
      justify-self: end;            /* right-align in the new row */
      margin-top: .12rem;
      margin-left: 0;
    }
  }
}
